@mixin image-basic() {
  max-width: 100%;
  height: auto;
  display: block;
}

@mixin reset-indent() {
  margin: 0;
  padding: 0;
}

@mixin reset-list() {
  list-style: none;
  margin: 0;
  padding: 0;
}

@mixin reset-decoration() {
  text-decoration: none;
}

@mixin reset-style-link {
  text-decoration: none;
}

@mixin font-basic ($size: $font-size--small, $line-height: $line-height--basic, $color: $basic-dark, $weight: $weight-normal, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
}

@mixin font-heading ($size: $font-size--big, $line-height: $line-height--large, $color: $basic-black, $weight: $weight-bold, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
}

@mixin font-heading-basic ($size: $font-size--basic, $line-height: $line-height--middle, $color: $basic-dark, $weight: $weight-bold, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
}

@mixin font-button-basic ($size: $font-size--big, $line-height: $line-height--petty, $color: $basic-black, $weight: $weight-bold, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
}

@mixin font-accent ($size: $font-size--basic, $line-height: $line-height--petty, $color: $basic-accent, $weight: $weight-bold, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
  text-transform: uppercase;
}

@mixin font-form ($size: $font-size--moderate, $line-height: $line-height--small, $color: $basic-black, $weight: $weight-bold, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
}

@mixin font-placeholder ($size: $font-size--moderate, $line-height: $line-height--small, $color: $basic-black-30a, $weight: $weight-normal, $style: $style-normal) {
  font-size: $size;
  line-height: $line-height;
  color: $color;
  font-weight: $weight;
  font-style: $style;
}

@mixin site-wrapper() {
  padding-right: 30px;
  padding-left: 30px;

  @media (min-width: $tablet-width) {
    padding-right: 50px;
    padding-left: 50px;
  }
}

@mixin site-padding() {
  padding-right: 30px;
  padding-left: 30px;

  @media (min-width: $tablet-width) {
    padding-right: 50px;
    padding-left: 50px;
  }
}

@mixin site-container() {
  margin: 0 auto;
  max-width: 1150px;
}

@mixin grid-padding-basic {
  padding-left: 170px;
}

@mixin grid-padding-desktop() {
  padding-left: $inner-padding--big;
}

@mixin button-basic {
  display: inline-block;
  min-width: 175px;
  padding-top: 17px;
  padding-bottom: 14px;
  padding-right: 20px;
  padding-left: 20px;
  background-color: $basic-white;
  border: 2px solid $basic-dark;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $basic-dark;
    color: $basic-white;
  }

  &:active {
    color: $basic-white-30a;
  }

  @media (min-width: $desktop-width) {
    padding-top: 19px;
  }
}
